import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Accordion from '@material-ui/core/Accordion';
import AccordionSummary from '@material-ui/core/AccordionSummary';
import AccordionDetails from '@material-ui/core/AccordionDetails';
import Typography from '@material-ui/core/Typography';
import TableCell from '@material-ui/core/TableCell';
import ExpandMoreIcon from '@material-ui/icons/ExpandMore';
import Paper from '@material-ui/core/Paper';
import Grid from '@material-ui/core/Grid';
import GridList from '@material-ui/core/GridList';
import GridListTile from '@material-ui/core/GridListTile';
import { Card, CardContent } from '@material-ui/core';

const useStyles = makeStyles((theme) => ({
    root: {
        flexGrow: 1,
        overflow: 'inherit'
    },
    cardContent: {
        textAlign: 'center',
        alignItems: 'center',
        overflow: 'inherit',
        width: 30,
        height: 10,
    },
    detalhes: {
        textAlign: 'center',
        flexDirection: 'center',
        justifyContent: 'center',
        wigth: 500,
    },
    detalhesBox: {
        width: 350,
        textAlign: 'center',
    },
    gridList: {
      width: 500,
      height: 450,
    },
}));

function calcularPorcentagem(peso, porcentagem) {
    return (peso * (porcentagem / 100)).toFixed(2)
}

export default function PorcentagemList(props) {
    const { exercicio, porcentagens } = props;
    const classes = useStyles();

    function montarGridList() {
        return (
            <GridList cellHeight={50}  cols={5}>
                {porcentagens.map((tile) => (
                    <GridListTile key={tile.valor} cols={tile.cols || 1}>
                        <Typography variant="button" display="block" gutterBottom>
                            {tile.valor}
                        </Typography>
                        <Typography >
                            {calcularPorcentagem(exercicio.personalRecord, tile.valor)}
                        </Typography>
                    </GridListTile>
                ))}
            </GridList>
        );
    }

    function montarPorcentagens(linha) {
        return (porcentagens.map((porcentagem) => (
            <div >
                <Grid item xs={12}>
                    <Card className={classes.root}>
                        <CardContent className={classes.cardContent}>
                            <Typography variant="button" display="block" gutterBottom>
                                {porcentagem.valor}
                            </Typography>
                        </CardContent>
                        <CardContent className={classes.cardContent}>
                            <Typography >
                                {calcularPorcentagem(exercicio.personalRecord, porcentagem.valor)}
                            </Typography>
                        </CardContent>
                    </Card>
                </Grid>
            </div>
        )));
    }

    return (
        <div className={classes.root}>
            <Accordion className={classes.root}>
                <AccordionSummary
                    expandIcon={<ExpandMoreIcon />}
                    aria-controls="panel1a-content"
                    id="panel1a-header">
                    <Typography>{exercicio.nome} {exercicio.personalRecord}</Typography>
                </AccordionSummary>
                <AccordionDetails className={classes.detalhes}>
                    <Grid container spacing={2} className={classes.detalhesBox}>
                        <Grid container item xs={12} spacing={2}>
                            {montarGridList()}
                        </Grid>
                    </Grid>
                </AccordionDetails>
            </Accordion>
        </div>
    );
}

